import angular from 'angular'
import addModalTemplate from './views/snapEBTAddModal.html'
import removeModalTemplate from './views/snapEbtRemoveModal.html'

export default angular
  .module('SnapEBTComponentModule', [])
  .controller('SnapEBTController', function ($rootScope, $scope, $uibModal, ebtCardService) {
    $scope.ebtCardService = ebtCardService

    $scope.openAddCardModal = function () {
      // eslint-disable-next-line no-unused-expressions
      $uibModal.open({
        template: addModalTemplate,
        controller: 'SnapEBTAddModalController',
        scope: $scope,
        controllerAs: '$ctrl',
      }).result
    }

    $scope.openRemoveCardModal = function () {
      // eslint-disable-next-line no-unused-expressions
      $uibModal.open({
        template: removeModalTemplate,
        controller: 'SnapEBTRemoveModalController',
        scope: $scope,
      }).result
    }

    $scope.ebtCardService.getEbtCard()
  })
